.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

html,
body {
  background-color: #00cec9;
  font-family: 'Varela Round', sans-serif;
  color: #f7f1e3;
  height: 100%;
  overflow: hidden;
}

#root {
  height: 100%;
}

.app-header {
  width: 100%;
  display: flex;
}

button {
  background-color: #81ecec;
  font-family: 'Varela Round', sans-serif;
  border: none;
  color: #f7f1e3;
}

.home-menu-ctn {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: none;
}

.home-menu {
  height: 50%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.button-home-menu {
  font-size: 30px;
  width: 80%;
  padding: 30px;
}

.action-button:active {
  transform: scale(0.7);
  transition-duration: 0.18s;
}

.back-btn {
  position: fixed;
  right: 20px;
  bottom: 20px;
  border-radius: 100px;
  padding: 16px 19px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.back-btn-img {
  width: 50px;
  height: 50px;
}

.RQS-title {
  text-align: center;
  font-size: 22px;
}

.RQS-buttons {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.RQS-button {
  font-size: 30px;
  padding: 10px 15px;
  margin: 5px 15px;
}

.QD-question {
  text-align: center;
  font-size: 35px;
  padding: 10px;
}

.QD-buttons {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}

.QD-button {
  font-size: 30px;
  padding: 15px 20px;
}